.fade-appear {
  opacity: 0;
}
.fade-appear-active {
  opacity: 1;
  transition: all 1000ms;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  right: -100px;
  background-color: red;
  transition: opacity 1000ms;
}
